* {
  box-sizing: border-box;
  font-synthesis: none;
}



.full-application-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 100vw;
  margin: 0;
}

.main-navbar {
  display: flex;
  align-self: center;
  justify-self: flex-start;
  width: 100%;
  margin-bottom: 50px;
  /* background-color: #FFD700; */
}

.home {
  display: flex;
  flex-direction: row;
  /* justify-content: center; */
  width: 100%;
  align-items: center;
  /* background-color: aqua; */
}

.left-container {
  align-self: center;
  justify-self: center;
  max-width: 40vw;
  text-transform: capitalize;

}


.image-home {
  width: 40vw;
  border-radius: 20px;
  box-shadow: 0 0 10px 0 whitesmoke;
}


.golden-span {
  color: #FFD700;
  text-transform: uppercase;
  text-shadow: 2px 2px 2px #000000;
  font-weight: 700;
  font-family: "Gochi Hand",cursive;
  
}


.navbar-left h1{
  font-size: 2em;
  font-weight: 700;
  color: #FFD700;
  text-transform: uppercase;
}

.navbar-left {
  flex: 1;
}

.my-outlet-containers {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  /* background-color: #FFD700; */
}