.custom-styles .excalidraw {
  --color-primary: #fcc6d9;
  --color-primary-darker: #f783ac;
  --color-primary-darkest: #e64980;
  --color-primary-light: #f2a9c4;
  background-color: black;
  color: #d64c7e;

}

.custom-styles .excalidraw.theme--dark {
  --color-primary: #d494aa;
  --color-primary-darker: #d64c7e;
  --color-primary-darkest: #e86e99;
  --color-primary-light: #dcbec9;
}

.all-sketches-page {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
}

.heart-svg {
  background-color: none;
  fill: #d64c7e;
  border-radius: 10px;
}

.new-sketch-card {
  border: 2px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 30px;
  border-radius: 15px;
  border-style: dashed;
  cursor: pointer;
}

.new-sketch-card:hover .heart-svg {
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.sketches-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  padding: 20px;
  margin-top: 10vh;
}

.new-sketch-card h2 {
  margin-top: 20px;
  font-size: 1.5rem;
  font-family: "Gochi Hand", cursive;
}

.sketch-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position : relative ;
}

.sketch-container h1 {
  margin-bottom: 30px;
}

.category-of-sketch {
  position: absolute;
  right: 30px;
  top: 10px;
  padding: 10px 15px ;
}

.excalidraw-container {
  position : relative ;
  margin-bottom: 0;
}

.maximizing-button {
  position: absolute;
  top : 3%;
  right: 10%;
  z-index: 10;
  background-color: transparent;
  color: black;
  font-size: 20px;
  border: none;
  padding: 5px 10px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
}