.summary-page-container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.my-summaries {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1rem;
  padding: 1rem;
  margin: 1rem;
  position: relative;
}



.empty-summary {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
  font-size: 2rem;
  font-weight: 700;
  color: #FFD700;
  text-transform: uppercase;
  text-shadow: 2px 2px 2px #000000;
  font-family: "Gochi Hand", cursive;
}

.create-new-summary {
  position: absolute;
  top: 5px;
  left: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
}

#create-new-summary {
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#create-new-summary:hover {
  color: #FFD700;
}

.new-summary-steps-pages {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 60vw;
  box-sizing: border-box;
  position: relative;
  /* background-color: #FFD700; */
}

.my-proggressBar {
  width: 98%;
}
.progress-bar {
  width: 98%;
}

.summary-step-title {
  margin-top: 30px;
  font-size: 2.2vw;
}

.step-one-container {
  display:  flex;
  flex-direction: row;
  /* justify-content: space-between; */
  align-items: center;
  margin-top: 30px;
  width: 100%;
}

.steps-summary-images {
  width: 50%;
  border-radius: 30px;
  box-shadow: 0 0 10px 0 whitesmoke;
}

.input-summary-step-one-fields {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  justify-content: center;
  margin-left: 30px;
  align-self: flex-start;
  padding-top: 40px;
}

.input-summary-step-one-fields input {
  /* margin: 10px; */
  margin-top: 10px;
  margin-bottom: 20px;
}

.input-summary-step-one-fields textarea {
  margin-top: 10px;
  margin-bottom: 20px;
  min-width: 30vw;
  max-height: 30vh;
  min-height: 15vh;
}
.meta-data-titles {
  font-size: 1.5vw;
  font-weight: 700;
  color: #FFD700;
  text-align: left;
  font-family: "Gochi Hand", cursive;
}

/* Style for the color input */
.color-input {
  appearance: none;
  /* Remove default styling */
  border: 2px solid #ccc;
  /* Add a border */
  border-radius: 4px;
  /* Add border radius */
  padding: 8px;
  /* Add padding */
  width: 200px;
  /* Set width */
  height: 30px;
  /* Set height */
  outline: none;
  /* Remove outline */
}

/* Style for the color preview box */
.color-input::-webkit-color-swatch-wrapper {
  padding: 0;
  /* Remove default padding */
}

.color-input::-webkit-color-swatch {
  border: none;
  /* Remove default border */
  border-radius: 4px;
  /* Add border radius */
  width: 30px;
  /* Set width */
  height: 100%;
  /* Set height */
  padding: 0;
  /* Remove padding */
}

#next-step {
  min-width: 110px;
  font-size: 3vh;
  width: 30%;
  align-self: flex-end;
  display: flex;
  justify-content: center;
  align-items: center;
}

#next-step:hover {
  background-color: #FFD700;
  color: #000000;
}

.radio-button-container {
  display: flex;
  flex-direction: column;
}

.radio-label {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.radio-input {
  display: none;
}

.radio-text {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
  width: 10vw;
  text-align: center;
}

.radio-input:checked+.radio-text {
  background-color: #FFD700;
  /* Change to desired color when selected */
  color: black;
  /* Change text color when selected */
  font-weight: 600;
}

.step-two-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

.step-two-meta-data-titles {
  font-size: 2.2vw;
  font-weight: 700;
  color: #FFD700;
  text-align: left;
  font-family: "Gochi Hand", cursive;
  text-align: center;
  margin-bottom: 20px;
}

.radio-button-container {
  /* background-color: #ccc; */
  padding: 20px 40px;
  border-radius: 20px;
  box-shadow: 0 0 10px 0 whitesmoke;
  margin: 20px;
  align-self: flex-start;
}

.check-box-text {
  font-size: 1.5vw;
  font-weight: 700;
  text-align: left;
  margin-left: 10px;
}

#container-submiting {
  opacity: 0.5;
  pointer-events: none;
  position: relative;
}

.step-three-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 10vh;
  background-color: aliceblue;
  width: 80vw;
  border-radius: 20px;
  box-shadow: 0 0 5px 0 whitesmoke;
  padding: 0px;
  height: 50vh;
  align-items: flex-start;
  justify-content: flex-start;
}

.editor-container {
  width: 100%;
  overflow-y: scroll;
}

#save-data {
  position: absolute;
  top: 10%;
  right: 10px;
}

.my-summaries {
  margin: 20px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1rem;
  padding: 1rem;
  position: relative;
  /* align the items in the middle*/
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  width: 80vw;
}

.empty-summary {
  color: #6272a4;
  text-align: center;
  align-self: center;
  position: absolute;
  left: 25%;
  margin-top: 30vh;
}

.summary-grid {
  display: grid;
  grid-template-columns: 3;
  gap: 20px;
}

.summary-card {
  background-color: #282a36;
  color: #f8f8f2;
  padding: 20px;
  border-radius: 10px;
  min-width: 20vw;
  max-width: fit-content;
}

.summary-card h3 {
  margin-top: 0;
}

.summary-card p {
  margin: 5px 0;
}

.summary-card p:first-child {
  font-weight: bold;
}

.summary-card p:not(:first-child) {
  opacity: 0.8;
}

.summary-card-meta-data {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-direction: column;
}